import { mapState, mapMutations } from 'vuex';
import {getOpenId, getUser, registerUser} from '@/api/user';
export default {
	computed:{
		...mapState(["code"])
	},
	mounted(){
		uni.setNavigationBarTitle({
		　　title:'健康生活'
		})
	},
	onLoad(){
	},
	data:function(){
		return {
			currentUser:null
		}
	},
	methods:{
		...mapMutations(["setCode", "setCurrentUser"]),
		onGetUserInfo(res){
			if(res.detail && res.detail.userInfo){
				this.$store.commit("setCurrentUser", res.detail.userInfo)
				this.currentUser = res.detail.userInfo;
				this.login();
				uni.showLoading({
					title: '登录中...'
				});
			}
		},
		login(){
			uni.login({
				provider:"weixin",
				success:res=>{
					this.currentUser.code = res.code;
					getOpenId(res.code).then(res=>{
						let data = JSON.parse(res.data.replace(/\'/g, '\"'))
						this.currentUser.OpenId = data.openid;
						return getUser(data.openid);
					}).then(res=>{
						if(res.data){
							this.currentUser = {
								...this.currentUser,
								...res.data
							};
							this.$store.commit('setCurrentUser', this.currentUser)
							uni.hideLoading()
							uni.switchTab({
								url:'../index/index'
							})
						} else {
							return registerUser({
								"OpenId":this.currentUser.OpenId,
								"Name":this.currentUser.nickName,
								"PSW":"123",
								"PhoneNumber":"123456"
							})
						}
					}).then(res=>{
						this.currentUser = {
							...this.currentUser,
							...res.data
						};
						this.$store.commit('setCurrentUser', this.currentUser)
						uni.hideLoading()
						uni.switchTab({
							url:'../index/index'
						})
					}).catch(res=>{
						uni.hideLoading();
					})
				},
			});
		},
	}
}
